import React, { memo, forwardRef, useMemo, useRef } from 'react';
import { noop } from 'lodash-es';
import { MergePropsType, mergeRestProps, spreadRestProps, mergeRef, cloneReactNode, useDefaultProp, useFocusTarget, renderReactNode, IRenderReactNode, KEY_CODES } from '../../common';
import { IconSelected } from '../icon';
import { ListWrap, ListItemWrap, ListItemPrefix, ListItemSuffix, ListItemContent, ListDivider, ListItemGroupTitle } from './styled';
import { IListItem, INestedItems, flattenNested, loopNested, isEnabled, isCustomized, isNormalized } from './nested';

type IListProps = MergePropsType<{
  /**
   * 列表项
   */
  items?: INestedItems<IListItem>;
  /**
   * 当前激活列表项（悬浮或键盘导航至的项）
   */
  active?: string | null;
  /**
   * 当前激活菜单项
   */
  onActiveChange?: (key: string | null, options: { from: 'mouse' | 'keyboard' }) => void;
  /**
   * 选中菜单项
   */
  onSelect?: (key: string) => void;
  /**
   * 响应键盘事件
   */
  keyboard?: boolean;
  /**
   * 自定义菜单项渲染
   */
  renderItem?: IRenderReactNode<{ key: string }>;
}, React.HTMLAttributes<HTMLDivElement>>;

export const List = memo(

  forwardRef(
    (props: IListProps, ref: React.ForwardedRef<HTMLDivElement>) => {

      const {
        items = [],
        active: _active,
        onActiveChange,
        onSelect = noop,
        keyboard = true,
        renderItem,
        ...rest
      } = props;

      const wrapRef = useRef<HTMLDivElement>(null);

      const [focusTargetId, focusTarget, focusOwner] = useFocusTarget(wrapRef);

      const [active, setActive] = useDefaultProp(undefined as string | undefined, _active, onActiveChange);

      const onKeyDown = keyboard ?
        (e: React.KeyboardEvent) => {

          const kbKey = e.key.toLowerCase();
          const isArrowUp = kbKey === 'arrowup' || e.keyCode === KEY_CODES.UP;
          const isArrowDown = kbKey === 'arrowdown' || e.keyCode === KEY_CODES.DOWN;
          const isEnter = kbKey === 'enter' || e.keyCode === KEY_CODES.ENTER;

          const flattenedItems = flattenNested(items);
          const flattenedEnabledKeys = flattenedItems.filter(isEnabled).map(it => it.key);

          // 键盘导航到 content 类型的选项，需尝试主动 focus
          const setActiveFromKeyboard = (key: string, current?: string) => {
            const targetItem = flattenedItems.find(it => it.key === key);

            if (targetItem) {

              const fn = () => setActive(key, { from: 'keyboard' });

              const currentItem = current ? flattenedItems.find(it => it.key === current) : undefined;

              if (isCustomized(targetItem)) {
                focusTarget(fn);
              } else if (currentItem && isCustomized(currentItem)) {
                focusOwner(fn);
              } else {
                fn();
              }
            }
          };

          const i = active ? flattenedEnabledKeys.findIndex(key => key === active) : -1;
          if (isArrowDown) {
            const j = i === -1 ? 0 : i + 1;
            if (j < flattenedEnabledKeys.length) {
              const nextKey = flattenedEnabledKeys[j];
              setActiveFromKeyboard(nextKey, active);
              e.stopPropagation();
            }
          } else if (isArrowUp) {
            const j = i === -1 ? flattenedEnabledKeys.length - 1 : i - 1;
            if (j >= 0) {
              const prevKey = flattenedEnabledKeys[j];
              setActiveFromKeyboard(prevKey, active);
              e.stopPropagation();
            }
          } else if (isEnter) {
            if (active !== undefined) {
              onSelect(active);
              e.stopPropagation();
            }
          }
        } :
        undefined;

      const sourceItems = useMemo(() => {

        const its: Array<IListItem | Parameters<Parameters<typeof loopNested>[2]>[0]> = [];

        loopNested(
          items,
          (it) => its.push(it),
          (it) => its.push(it),
          (s) => ({ key: s, title: s })
        );

        return its;
      }, [items]);

      // 列表项中，若有任意一项有前缀/后缀，那么其他的项即使没有前后缀，也应空出前后缀的位置
      const [hasPrefix, hasSuffix] = useMemo(() => {
        const normalizedItems = flattenNested(items).filter(isNormalized);
        return [
          normalizedItems.findIndex(it => !!it.prefix) !== -1,
          normalizedItems.findIndex(it => !!it.suffix || !!it.selected) !== -1
        ]
      }, [items]);

      return <ListWrap
        ref={mergeRef(ref, wrapRef)}
        tabIndex={-1}
        {...mergeRestProps(rest, 'wdn-list', {}, { onKeyDown })}
      >
        {
          sourceItems.map((it) => {

            if (Array.isArray(it) && it[0] === 'divider') {

              return <ListDivider key={it[1]} role='separator' />;
            } else if (Array.isArray(it) && it[0] === 'group') {

              return <ListItemGroupTitle key={it[2]}>{it[1]}</ListItemGroupTitle>;
            } else if ('content' in it) {

              return <React.Fragment key={it.key}>
                {
                  cloneReactNode(it.content,
                    {
                      key: it.key,
                      id: focusTargetId,
                      onMouseEnter: () => setActive(it.key, { from: 'mouse' }),
                      onMouseLeave: () => setActive(undefined, { from: 'mouse' })
                    }
                  )
                }
              </React.Fragment>
            } else {

              const prefix: (p: React.ReactNode) => React.ReactNode = hasPrefix ? (p => p || null) : (p => p);
              const suffix: (p: React.ReactNode) => React.ReactNode = hasSuffix ? (p => p || null) : (p => p);

              return renderReactNode(
                renderItem,
                <ListItem
                  key={it.key}
                  prefix={prefix(it.prefix)}
                  suffix={suffix(it.selected ? <IconSelected /> : it.suffix)}
                  active={it.key === active}
                  disabled={it.disabled}
                  onMouseEnter={it.disabled ? undefined : () => setActive(it.key, { from: 'mouse' })}
                  onMouseLeave={it.disabled ? undefined : () => setActive(undefined, { from: 'mouse' })}
                  onClick={it.disabled ? undefined : () => onSelect(it.key)}
                  {...spreadRestProps(rest, it.key)}
                >
                  {it.title}
                </ListItem>,
                { key: it.key }
              )
            }
          }
          )
        }
      </ListWrap>;
    }
  )
);

List.displayName = 'List';

type IListItemProps = MergePropsType<{
  /**
   * 列表项的内容
   */
  children?: React.ReactNode;
  /**
   * 是否激活
   */
  active?: boolean;
  /**
   * 前缀
   */
  prefix?: React.ReactNode;
  /**
   * 后缀
   */
  suffix?: React.ReactNode;
  /**
   * 禁用
   */
  disabled?: boolean;
}, React.HTMLAttributes<HTMLDivElement>>;

export const ListItem = memo(

  forwardRef(
    (props: IListItemProps, ref: React.ForwardedRef<HTMLDivElement>) => {
      const {
        children = '',
        active = false,
        prefix,
        suffix,
        disabled = false,
        ...rest
      } = props;

      return <ListItemWrap ref={ref} isActive={active} disabled={disabled} {...mergeRestProps(rest, 'wdn-listitem')}>
        {prefix !== undefined ? <ListItemPrefix>{prefix}</ListItemPrefix> : null}
        <ListItemContent>{children}</ListItemContent>
        {suffix !== undefined ? <ListItemSuffix>{suffix}</ListItemSuffix> : null}
      </ListItemWrap>;
    }
  )
);

ListItem.displayName = 'ListItem';